            die Lieferung von Waren, die ein Unternehmer (nachfolgend Kunde) mit dem Verkufer hinsichtlich der vom
            Verkufer angebotenen Waren abschliet. Hiermit wird der Einbeziehung von eigenen Bedingungen des Kunden
            widersprochen, es sei denn, es ist etwas anderes vereinbart.<br />1.2 Diese AGB gelten auch ausschlielich,
            wenn der Verkufer in Kenntnis entgegenstehender oder von diesen Bedingungen abweichender Bedingungen des
