            Bei Lieferungen in Lnder auerhalb der Europischen Union knnen im Einzelfall weitere Kosten anfallen, die
            der Verkufer nicht zu vertreten hat und die vom Kunden zu tragen sind. Hierzu zhlen beispielsweise Kosten
