            Rechnungsbetrag nach Erteilung eines SEPA-Lastschriftmandats, nicht jedoch vor Ablauf der Frist fr die
            Vorabinformation zur Zahlung fllig. Der Einzug der Lastschrift erfolgt, wenn die bestellte Ware das Lager
            des Verkufers verlsst, nicht jedoch vor Ablauf der Frist fr die Vorabinformation. Vorabinformation
            ("Pre-Notification") ist jede Mitteilung (z.B. Rechnung, Police, Vertrag) des Verkufers an den Kunden, die
