            oder aufgrund der Angabe einer falschen Bankverbindung nicht eingelst oder widerspricht der Kunde der
            Abbuchung, obwohl er hierzu nicht berechtigt ist, hat der Kunde die durch die Rckbuchung des jeweiligen
            Kreditinstituts entstehenden Gebhren zu tragen, wenn er dies zu vertreten hat. Der Verkufer behlt sich
            vor, bei Auswahl der Zahlungsart SEPA-Lastschrift eine Bonittsprfung durchzufhren und diese Zahlungsart
