            nicht ordnungsgemer Selbstbelieferung vom Vertrag zurckzutreten. Dies gilt nur fr den Fall, dass die
            Nichtlieferung nicht vom Verkufer zu vertreten ist und dieser mit der gebotenen Sorgfalt ein konkretes
