        <h3><br />8 Mngelhaftung</h3>
        <p><br />Ist die Kaufsache mangelhaft, gelten die Vorschriften der gesetzlichen Mngelhaftung. Hiervon
            abweichend gilt:<br />8.1 Mngelansprche entstehen nicht bei natrlicher Abnutzung oder Schden, die nach
            dem Gefahrbergang infolge fehlerhafter oder nachlssiger Behandlung, bermiger Beanspruchung,
            ungeeigneter Betriebsmittel oder die aufgrund besonderer uerer Einflsse entstehen, die nach dem Vertrag
            nicht vorausgesetzt sind. Werden vom Kunden oder von Dritten unsachgem nderungen oder
            Instandsetzungsarbeiten vorgenommen, so bestehen fr diese und die daraus entstehenden Folgen ebenfalls
            keine Mngelansprche, es sei denn der Kunde kann nachweisen, dass die gergte Strung nicht durch diese
            nderungen oder Instandsetzungsarbeiten verursacht worden sind.<br />8.2 Bei neuen Waren betrgt die
            Verjhrungsfrist fr Mngelansprche ein Jahr ab Ablieferung der Ware.<br />8.3 Bei gebrauchten Waren sind
            die Rechte und Ansprche wegen Mngeln ausgeschlossen.<br />8.4 Die vorstehend geregelten
            Haftungsbeschrnkungen und Verjhrungsfristverkrzungen gelten nicht</p>
        <ul>
            <li>fr Schadensersatz- und Aufwendungsersatzansprche des Kunden,</li>
            <li>fr den Fall, dass der Verkufer den Mangel arglistig verschwiegen hat, sowie</li>
            <li>fr den Rckgriffsanspruch nach  445a BGB.</li>
        </ul>
        <p><br />8.5 Der Verkufer hat im Falle der Nacherfllung das Wahlrecht zwischen Nachbesserung oder
            Ersatzlieferung.<br />8.6 Erfolgt im Rahmen der Mngelhaftung eine Ersatzlieferung, beginnt die Verjhrung
            nicht erneut.<br />8.7 Ist die Nacherfllung im Wege der Ersatzlieferung erfolgt, ist der Kunde
            verpflichtet, die zuerst gelieferte Ware innerhalb von 30 Tagen an den Verkufer zurckzusenden. Das
            Rcksendepaket muss den Grund der Rcksendung, den Kundennamen und die fr den Kauf der mangelhaften Ware
            vergebene Nummer enthalten, die dem Verkufer die Zuordnung der zurckgesandten Ware ermglicht. Solange und
            soweit die Zuordnung der Rcksendung aus Grnden, die der Kunde zu vertreten hat, nicht mglich ist, ist der
